import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../Context/CartContext'
import { useNavigate } from 'react-router-dom'
import { toast } from 'react-hot-toast'

export default function Cart() {
    let navigate = useNavigate()
    let { getFromCart, UpdateCart, DeleteItem } = useContext(CartContext)

    const [cartdata, setCartData] = useState([])
    async function getFromCartFun() {
        let data = await getFromCart()
        if(data.status == 'success')
        setCartData(data.data.products)
    }

    async function UpdateCartFun(id, count) {
        let data = await UpdateCart(id, count)
        if (data.status == 'success')
            setCartData(data.data.products)
        if (count == 0)
            DeleteItemFun(id)
    }

    async function DeleteItemFun(id) {
        let data = await DeleteItem(id)
        setCartData(data.data.products)
    }

    function popOut()
    {
        if(localStorage.getItem('token'))
            navigate('/checkout')
        else
        toast(<p>you should log in first</p>)
    }

    useEffect(() => {
        getFromCartFun()
    }, [])
    return (
        <div>
            {cartdata.length > 0 ? <table className='table text-center my-5 table-striped '>
                <thead>
                    <tr>
                      <th>Index</th>
                      <th>Title</th>
                      <th>price</th>
                      <th>Image</th>
                      <th>Count</th>
                      <th>Remove</th>
                    </tr>
                </thead>
                <tbody>
                    {cartdata.length > 0 ? cartdata.map((prod, index) => <tr key={prod._id}>

                        <td style={{ lineHeight: '100px' }}>{index + 1}</td>
                        <td style={{ lineHeight: '100px' }}>{prod.product.title}</td>
                        <td style={{ lineHeight: '100px' }}>{prod.price}</td>
                        <td style={{ lineHeight: '100px' }}><img src={prod.product.imageCover} style={{ height: '100px' }} alt="" /></td>
                        <td style={{ lineHeight: '100px' }}>
                            <i style={{ cursor: 'pointer' }} className="fa-solid fa-chevron-left" onClick={() => { UpdateCartFun(prod.product._id, prod.count - 1) }}></i>
                            <span className='mx-2'>{prod.count}</span>
                            <i style={{ cursor: 'pointer' }} className="fa-solid fa-chevron-right" onClick={() => { UpdateCartFun(prod.product._id, prod.count + 1) }}></i>
                        </td>
                        <td>
                            <i style={{ lineHeight: '100px', cursor: 'pointer' }} className='fa-solid fa-trash text-danger' onClick={() => { DeleteItemFun(prod.product._id) }}></i>
                        </td>
                    </tr>) : 'loading'}
                </tbody>
            </table>:<h2 className='my-4 text-success'>your Cart is empty</h2>}
            
            <button className='btn btn-success' onClick={popOut}>CheckOut</button>
        </div>
    )
}
